import "zone.js"; // Required for Angular
import { platformBrowserDynamic } from "@angular/platform-browser-dynamic";
import AppModule from "./app/app.module";

Office.initialize = () => {
  // Bootstrap the app
  platformBrowserDynamic()
    .bootstrapModule(AppModule)
    .catch(error => console.error(error));

    var itemId = "";

    // Display error messages into Word addin
    function displayError(message) {
      // $("p.totem-addin-error").css("display", "inline");
      // $("p.totem-addin-error").text(message);
    }
    
    var result = "";
    var counter = 1;
    
    // Format XML datasource definition to JSTree JSON format
    function mapXMLToJSTreeFormat(entry, parent) {
      for (var i = 0; i < entry.childNodes.length ; i++) {
        var node = entry.childNodes[i];
        var id = counter;
        counter++;
        
        if(node.childNodes.length !== 0 && node.childNodes[0].nodeType !== 3) {
          result += '{"id": "' + id + '", "parent": "' + parent + '", "text": "' + node.nodeName + '" },';
          mapXMLToJSTreeFormat(node, id);
        } else {
          result += '{"id": "' + id + '", "parent": "' + parent + '", "text": "' + node.nodeName + '", "icon": "jstree-file" },';
        }
      }
      
      return result;
    }

    $(function() {
      // Build a JSTree based on Custom XML Part datasource definition contained in template document
      Office.context.document.customXmlParts.getByNamespaceAsync("http://opendope.org/datasourceDefinition", function (result) {
        if(result.status !== "succeeded" || result.value.length === 0) {
          displayError("Error when trying to get datasource definition Custom XML Part by namespace from current template document");
          return;
        }

        var xmlPart = result.value;
        if (xmlPart.length !== 0) {
          itemId = xmlPart[0].id;
          
          Office.context.document.customXmlParts.getByIdAsync(itemId, function (result) {
            if(result.status !== "succeeded") {
              console.log("Error when trying to get datasourcedefinition Custom XML Part by id");
              return;
            }
            
            var xmlPart = result.value;
            xmlPart.getXmlAsync(function (result) {
              console.log(result);
              if(result.status !== "succeeded") {
                console.log("Error when trying to get node XML");
                return;
              }
              
              var xml = $.parseXML(result.value);
              console.log(xml);
              var json = mapXMLToJSTreeFormat(xml, "#");
              json = json.substring(0, json.length - 1);
              console.log(json);
              
              // var body = '{"core": {"data": [TREE]}}';
              // var data = body.replace("TREE", json);
              
              // $('#tree').jstree($.parseJSON(data));
            });
          });
        }
      });
    });
};
